@import url(http://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300ita‌​lic,400italic,500,500italic,700,700italic,900italic,900);
li{
    display: -webkit-flex;
    display: flex;
    padding: .5em 1em;
    background-color: lightblue;

    border-radius: 0.5em;
}
li > *:not(:last-child){
    margin-right: 1em;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
ul > *:not(:last-child) {
    margin-bottom: .5em;
}

.wrapper {

    padding: 2em;
    margin: 2em;
    background-color: cadetblue;
    border-radius: 1em;
}

.middle-block {
    margin: 2em 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: end;
            justify-content: end;

}
.middle-block > *{
    margin-left: 2em;
    padding: 1em;
    background-color: #b6d7d7;
    border-radius: 0.5em;
}
.announcer {
    opacity:  0;
    transition: opacity 1s;
}

.announcer-error {
    border-color: crimson;
    background-color: rgba(220,20,60,0.5);
}

.announcer-success {
    background-color: #b6d7d7;
}
.visible {
    opacity: 1;
}
.invisible {
    opacity: 0;
}

*{
    color: #424242;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

